export default {
	data() {
		return {
			Router: this.$Router,
			showView: false,
			is_show: false,
			show_addok: false,
			is_show1: false,
			mainData: {},
			chooseSku: {},
			noSelectSku: true,
			count: 1,
			price: '',
			groupPrice: '',
			hasAdd: false,
			isVip: false,
			groupData: [],
			countDownList: [],
			endTimeList: [],
			chooseGroupData: {},
			chooseGroupIndex: -1,
			total: 0,
			hasOrder:false
		}
	},

	onLoad(options) {
		const self = this;
		self.id = options.id;
		if (options.user_no) {
			self.user_no = options.user_no;
			self.isVip = true
		};
		if (options.leader_no) {
			self.leader_no = options.leader_no
		};
		if (options.order_id) {
			self.order_id = options.order_id;
			self.hasOrder = true
		};
		self.paginate = self.$Utils.cloneForm(self.$AssetsConfig.paginate);
		self.$Utils.loadAll(['getMainData', 'getUserInfoData'], self);
	},

	onUnload() {
		const self = this;
		//清除计时器  即清除setInter
		clearTimeout(self.countDown())
	},

	methods: {

		getUserInfoData() {
			const self = this;
			console.log('852369')
			const postData = {};
			postData.tokenFuncName = 'getProjectToken'
			postData.searchItem = {
				user_no: uni.getStorageSync('user_info').user_no
			};
			const callback = (res) => {
				if (res.solely_code == 100000 && res.info.data[0]) {
					self.userInfoData = res.info.data[0];
				} else {
					self.$Utils.showToast(res.msg, 'none')
				};
				self.$Utils.finishFunc('getUserInfoData');
			};
			self.$apis.userInfoGet(postData, callback);
		},

		addToShop() {
			const self = this;
			if (self.hasAdd) {
				self.$Utils.showToast('商品已在店铺', 'none');
				return
			};
			if (self.userInfoData.level >= 3 && self.userInfoData.check_status == 2) {
				const postData = {};
				postData.data = {
					thirdapp_id: 2,
					type: 6,
					relation_table: 'product',
					relation_id: self.id,
					user_no: uni.getStorageSync('user_info').user_no,
				};
				postData.tokenFuncName = 'getProjectToken';
				const callback = (res) => {
					if (res.solely_code == 100000) {
						self.show_addok = true;
						self.getMainData()
					} else {
						self.$Utils.showToast(res.msg, 'none');
					}
				};
				self.$apis.logAdd(postData, callback);
			} else {
				self.$Utils.showToast('您暂未拥有店铺', 'none');
			}
		},

		counter(type) {
			const self = this;
			if (type == '+') {
				self.count++
			} else if (type == '-' && self.count > 1) {
				self.count--
			};
			self.countTotalPrice()
		},

		countTotalPrice() {
			const self = this;
			self.price = self.chooseSku.price * self.count.toFixed(2);

			self.groupPrice = self.chooseSku.group_price * self.count.toFixed(2)
		},

		viewMoreGroup() {
			const self = this;
			var orderList = {
				sku: [{
					id: self.chooseSku.id,
					count: self.count,
					sku: self.chooseSku
				}],
				type: self.mainData.type,
			};
			uni.setStorageSync('payPro', orderList);
			if (self.leader_no) {
				self.$Router.navigateTo({
					route: {
					path: '/pages/goodsDetail_pintuan/goodsDetail_pintuan?product_no=' + self.mainData.product_no + '&leader_no=' +self.leader_no
					},

				})
			} else {
				self.$Router.navigateTo({
					route: {
					path: '/pages/goodsDetail_pintuan/goodsDetail_pintuan?product_no=' + self.mainData.product_no
					}
				})
			}
		},

		goBuy(type) {
			const self = this;
			if (JSON.stringify(self.chooseSku) == '{}') {
				self.$Utils.showToast('商品错误', 'none', 1000);
				return;
			};
			var orderList = {
				sku: [{
					id: self.chooseSku.id,
					count: self.count,
					sku: self.chooseSku
				}],
				type: self.mainData.type,
			};
			uni.setStorageSync('payPro', orderList);
			if (JSON.stringify(self.chooseGroupData) != '{}') {
				uni.setStorageSync('group_no', self.chooseGroupData.group_no)
			};
			if (self.leader_no) {
				self.$Router.navigateTo({
					route: {
						path: '/pages/confirmOrder/confirmOrder?type=' + type + '&leader_no=' + self.leader_no
					}
				})
			} else {
				self.$Router.navigateTo({
					route: {
						path: '/pages/confirmOrder/confirmOrder?type=' + type
					}
				})
			}
		},
		//选择商品属性
		seltSpecs(index) {
			const self = this;
			if (self.mainData.sku[index].id != self.chooseSku.id) {
				self.chooseSku = self.mainData.sku[index];
				self.count = 1;
				self.countTotalPrice()
			}

		},

		goPinDan() {
			const self = this;
			self.is_show = !self.is_show;
		},

		chooseGroup(index) {
			const self = this;
			self.is_show = true;
			self.chooseGroupIndex = index;
			self.chooseGroupData = self.groupData[index];
			console.log('self.countDownList', self.chooseGroupIndex)
			console.log('self.countDownList', self.countDownList)
		},

		showSel() {
			const self = this;
			self.is_show1 = !self.is_show1
		},

		showAddok() {
			const self = this;
			self.show_addok = !self.show_addok
		},

		prev() {
			const self = this;
			self.$Router.switchTab({
				route: {
					path: '/pages/index/index'
				}
			})
		},

		onShareAppMessage: function(ops) {
			console.log(ops)
			const self = this;
			if (ops.from === 'button') {
				// 来自页面内转发按钮
				if(ops.target.id==1){
					if(self.groupData.length==0){
						return {
							title: self.mainData.title,
							imageUrl: self.mainData.mainImg[0]&&self.mainData.mainImg[0].url?self.mainData.mainImg[0].url:'',
							desc: '小蜂拼拼社区团购',
							path: '/pages/goodsJoinDetail/goodsJoinDetail?id=' + self.mainData.id + '&leader_no=' + self.user_no, //点击分享的图片进到哪一个页面
							success: function(res) {
								// 转发成功
								console.log("转发成功:" + JSON.stringify(res));
							},
							fail: function(res) {
								// 转发失败
								console.log("转发失败:" + JSON.stringify(res));
							}
						}	
					}else{
						return {
							title: self.mainData.title,
							imageUrl: self.mainData.mainImg[0]&&self.mainData.mainImg[0].url?self.mainData.mainImg[0].url:'',
							desc: '小蜂拼拼社区团购',
							path: '/pages/goodsJoinDetail/goodsJoinDetail?id=' + self.mainData.id + '&order_id=' + self.groupData[0].id, //点击分享的图片进到哪一个页面
							success: function(res) {
								// 转发成功
								console.log("转发成功:" + JSON.stringify(res));
							},
							fail: function(res) {
								// 转发失败
								console.log("转发失败:" + JSON.stringify(res));
							}
						}
					}
				}else if(ops.target.id==2){
					return {
						title: self.mainData.title,
						imageUrl: self.mainData.mainImg[0]&&self.mainData.mainImg[0].url?self.mainData.mainImg[0].url:'',
						desc: '小蜂拼拼社区团购',
						path: '/pages/goodsJoinDetail/goodsJoinDetail?id=' + self.mainData.id, //点击分享的图片进到哪一个页面
						success: function(res) {
							// 转发成功
							console.log("转发成功:" + JSON.stringify(res));
						},
						fail: function(res) {
							// 转发失败
							console.log("转发失败:" + JSON.stringify(res));
						}
					}
				}
				
				
				console.log(ops.target)
			} else {
				return {
					title: self.mainData.title,
					imageUrl: self.mainData.mainImg[0]&&self.mainData.mainImg[0].url?self.mainData.mainImg[0].url:'',
					desc: '小蜂拼拼社区团购',
					path: '/pages/goodsJoinDetail/goodsJoinDetail?id=' + self.mainData.id, //点击分享的图片进到哪一个页面
					success: function(res) {
						// 转发成功
						console.log("转发成功:" + JSON.stringify(res));
					},
					fail: function(res) {
						// 转发失败
						console.log("转发失败:" + JSON.stringify(res));
					}
				}
				console.log(ops.target)
			}
		},

		getMainData() {
			const self = this;
			const postData = {};
			postData.searchItem = {
				thirdapp_id: 2,
				type: 1,
				id: self.id
			};
			postData.getAfter = {
				sku: {
					tableName: 'Sku',
					middleKey: 'product_no',
					key: 'product_no',
					searchItem: {
						status: 1,
					},
					condition: '='
				},

				log: {
					tableName: 'Log',
					middleKey: 'id',
					key: 'relation_id',
					searchItem: {
						status: 1,
						type: 6,
						user_no: ['in', uni.getStorageSync('user_info').user_no]
					},
					condition: '='
				},
			};
			postData.saveFunction = [{
				FuncName: 'viewProduct',
				searchItem: {
					id: self.id
				}
			}];
			const callback = (res) => {
				if (res.info.data.length > 0) {
					self.mainData = res.info.data[0];
					if (self.mainData.sku[0]) {
						self.chooseSku = self.mainData.sku[0];
						self.countTotalPrice();
						self.getGroupData()
					} else {
						self.$Utils.showToast('商品信息错误', 'none');
						setTimeout(function() {
							uni.navigateBack({
								delta: 1
							})
						}, 1500);
					};
					if (self.mainData.log.length > 0) {
						self.hasAdd = true
					};
					const regex = new RegExp('<img', 'gi');
					self.mainData.content = self.mainData.content.replace(regex, `<img style="max-width: 100%;"`);
				} else {
					self.$Utils.showToast('没有更多了', 'none');
				};
				console.log('self.mainData', self.mainData)
				self.$Utils.finishFunc('getMainData');
			};
			self.$apis.productGet(postData, callback);
		},

		getGroupData() {
			const self = this;
			console.log('852369')
			var now = Date.parse(new Date());
			const postData = {};
			postData.tokenFuncName = 'getProjectToken'
			postData.paginate = {
				count: 0,
				currentPage: 1,
				is_page: true,
				pagesize: 5
			};
			postData.searchItem = {
				user_type: 0,
				order_step: 4,
				pay_status: 1,
				product_no: self.mainData.product_no,
				group_leader: 'true',
				invalid_time: ['>', now]
			};
			if(self.leader_no){
				postData.searchItem.leader_no = self.leader_no;
				delete postData.searchItem.order_step
			};
			if(self.user_no){
				postData.searchItem.leader_no = self.user_no;
				delete postData.searchItem.order_step
			};
			postData.getAfter = {
				groupMember: {
					tableName: 'Order',
					middleKey: 'group_no',
					key: 'group_no',
					searchItem: {
						status: 1,
						pay_status: 1
						//group_leader:'false'
					},
					condition: '='
				},
				user: {
					tableName: 'User',
					middleKey: 'user_no',
					key: 'user_no',
					searchItem: {
						status: 1,
					},
					condition: '='
				}
			};
			const callback = (res) => {
				if (res.solely_code == 100000 && res.info.data[0]) {
					self.groupData.push.apply(self.groupData, res.info.data);
					for (var i = 0; i < self.groupData.length; i++) {
						self.endTimeList.push({
							actEndTime: self.groupData[i].invalid_time
						})
						self.groupData[i].lessNum = parseInt(self.groupData[i].standard) - parseInt(self.groupData[i].groupMember.length)
					};
					if(self.leader_no){
						self.is_show = true;
						self.chooseGroupIndex = self.groupData.length - 1;
						self.chooseGroupData = self.groupData[self.groupData.length - 1];
					}
				};
				self.countDown();
				if (self.order_id) {
					self.getShareOrder()
				};
				self.total = res.info.total;
				self.$Utils.finishFunc('getUserInfoData');
			};
			self.$apis.orderGet(postData, callback);
		},

		getShareOrder() {
			const self = this;
			console.log('852369')
			var now = Date.parse(new Date());
			const postData = {};
			postData.tokenFuncName = 'getProjectToken'
			postData.paginate = {
				count: 0,
				currentPage: 1,
				is_page: true,
				pagesize: 5
			};
			postData.searchItem = {
				user_type: 0,
				//order_step: 4,
				pay_status: 1,

				group_leader: 'true',
				invalid_time: ['>', now],
				id: self.order_id
			};
			postData.getAfter = {
				groupMember: {
					tableName: 'Order',
					middleKey: 'group_no',
					key: 'group_no',
					searchItem: {
						status: 1,
						pay_status: 1
						//group_leader:'false'
					},
					condition: '='
				},
				user: {
					tableName: 'User',
					middleKey: 'user_no',
					key: 'user_no',
					searchItem: {
						status: 1,
					},
					condition: '='
				}
			};
			const callback = (res) => {
				if (res.solely_code == 100000 && res.info.data[0]) {
					self.groupData.push.apply(self.groupData, res.info.data);
					for (var i = 0; i < self.groupData.length; i++) {
						self.endTimeList.push({
							actEndTime: self.groupData[i].invalid_time
						})
						self.groupData[i].lessNum = parseInt(self.groupData[i].standard) - parseInt(self.groupData[i].groupMember.length)
					}
					self.is_show = true;
					self.chooseGroupIndex = self.groupData.length - 1;
					self.chooseGroupData = self.groupData[self.groupData.length - 1];
					console.log('self.countDownList', self.chooseGroupIndex)
					console.log('self.countDownList', self.countDownList)
				} else {
					self.$Utils.showToast('团数据异常', 'none');
				}
				self.countDown();

				self.$Utils.finishFunc('getUserInfoData');
			};
			self.$apis.orderGet(postData, callback);
		},

		timeFormat(param) { //小于10的格式化函数
			const self = this;
			return param < 10 ? '0' + param : param;
		},

		countDown() { //倒计时函数
			// 获取当前时间，同时得到活动结束时间数组
			const self = this;
			let newTime = new Date().getTime();
			let endTimeList = self.endTimeList;
			let countDownArr = [];
			// 对结束时间进行处理渲染到页面
			for (var i = 0; i < self.endTimeList.length; i++) {
				let endTime = self.endTimeList[i].actEndTime;
				let obj = null;
				// 如果活动未结束，对时间进行处理
				if (endTime - newTime > 0) {
					let time = (endTime - newTime) / 1000;
					// 获取天、时、分、秒
					let day = parseInt(time / (60 * 60 * 24));
					let hou = parseInt(time % (60 * 60 * 24) / 3600);
					let min = parseInt(time % (60 * 60 * 24) % 3600 / 60);
					let sec = parseInt(time % (60 * 60 * 24) % 3600 % 60);
					if (day > 0) {
						hou = hou + day * 24
					}
					obj = {
						hou: self.timeFormat(hou),
						min: self.timeFormat(min),
						sec: self.timeFormat(sec)
					}
				} else { //活动已结束，全部设置为'00'
					obj = {
						hou: '00',
						min: '00',
						sec: '00'
					}
				}
				countDownArr.push(obj);

			}
			// 渲染，然后每隔一秒执行一次倒计时函数
			self.countDownList = countDownArr;

			setTimeout(this.countDown, 1000);
		},
	},
};
